namespace $ { export class $mol_icon extends $mol_svg {

	/// viewBox \0 0 24 24
	viewBox() {
		return "0 0 24 24"
	}

	/// attr * viewBox <= viewBox
	attr() {
		return ( { ...super.attr() , 
			"viewBox" :  <any> this.viewBox() ,
		} )
	}

	/// path \
	path() {
		return ""
	}

	/// pather $mol_svg_path geometry <= path
	@ $mol_mem()
	pather() {
		return new $mol_svg_path().setup( obj => { 
			obj.geometry = () => this.path()
		} )
	}

	/// sub / <= pather
	sub() {
		return [].concat( this.pather() )
	}

} }

